@use "./mixin" as m;

#root {
  section.menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    h1{
      margin-bottom: 60px;
    }
    form.theme {
      legend {
        margin-bottom: 10px;
        font-weight: 800;
        font-size: 0.8em;
        text-align: center;
      }
      div {
        display: flex;
        width: 180px;
        height: 30px;
        border-radius: 30px;
        border: 1px solid #eee;
        box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        input {
          display: none;
          &:checked + label{
            opacity: 1;
          }
          &:checked + label::before {
            display: block;
          }
        } //input
        label {
          position: relative;
          flex: 1;
          cursor: pointer;
          @include m.icon(before, "\f00c");
          opacity: 0.3;
          &:hover {
            opacity: 1;
          }
          &[for="radio1"] {
            background: #999;
          }
          &[for="radio2"] {
            background: rgb(189, 235, 254);
          }
          &[for="radio3"] {
            background: bisque;
          }
          &::before {
            @include m.posLT(absolute, 50%, 50%, -50%, -50%);
            display: none;
            color:#FFF;
          }
        } //label
      }
    } //theme
    nav {
      margin-top: 40px;
      font-weight: 600;
      text-transform: uppercase;
      a{
        display: block;
        margin: 20px 0;
        padding: 7px 25px;
        box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
        border-radius: 20px;
        text-align: center;
        &:hover{
          text-shadow: -2px -2px 1px rgba(255, 255, 255, 1), 2px 2px 1px rgba(0, 0, 0, 0.1);
        }
        &:active{
          box-shadow: -2px -2px 5px rgba(0, 0, 0, .2), 3px 3px 5px rgba(255, 255, 255, 1);
        }
        i{
          margin-right: 10px;
        }
      }
    }
  } //section
} //root
