@use 'mixin' as m;
section.dday{
  display: flex;
  flex-direction: column;

  article.dday-list{
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1; 
    &::before{
      pointer-events: none;
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: url(../assets/img/timer-icon-bg.gif);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      mix-blend-mode: overlay;
      opacity: 0.8;
      filter: grayscale(1) brightness(50%) ;
    }//before

    .filter-btn{
      position: absolute;
      left:calc(50% - 20px);
      border-radius: 0 0 5px 5px;
      width: 40px;
      height: 40px;
      background: rgba(#000, 0.6);
    
      z-index: 1;
      transition: all 0.2s;
      &:hover{
        font-size: 1.2em;
      }
      i:last-child{
        display: none;
      }
      &.active{
        color:#FFF;
        i:first-child{
          display: none;
        }
        i:last-child{
          display: block;
        }
      }
    }//filter-btn

    .filter-form{
      position: absolute;
      top:0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      height: 100%;
      padding: 15px;
      box-sizing: border-box;
      background: rgba(#FFF,0.7);
      transform: translateY(-100%);
      transition: transform 1s;
      &.active{
        transform: translateY(0%);
      }
      fieldset{
        margin-bottom: 15px;
        background: rgba(#000,0.3);
        border-radius: 5px;
        padding: 10px;
        legend{
          font-size: 0.9em;
          padding: 5px 10px;
          background: rgba(#000,0.7);
          color:#FFF;
          border-radius: 5px;
        }//legend
        button{
          width: 100%;
          margin-top: 10px ;
          padding: 10px 0;
          outline: none;
          box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
          background: #FFF ;
          &:active {
            box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
          } //button:active
        }//button
        input{
          width: 100%;
          box-sizing: border-box;
          padding: 10px;
          border: none;
          outline: none;
          box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
          &:focus {
            background: #000;
            color:#FFF;
          }
        }//input
      }//fieldset
    }//filter-form

    >ul{
      flex:1 0 1px;
      overflow: auto;
      margin: 15px 0;
      >li{
        &.active{
          background:rgba(red,0.3);
        }
        &.deactive{
          background:rgba(#000,0.5);
        }
        cursor: pointer;
        border-radius: 10px;
        padding: 15px  10px;
        border-bottom: 2px groove rgba(#FFF,0.5);
        div.meta{
          display: flex;
          align-items: center;
          justify-content: space-between;
          h3{
            max-width: 60%;
            font-weight: 600;
            font-size: 1.1em;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          p{
            time{
              display: block;
              text-align: center;
              font-size: 0.8em;
              small,em{
                margin: 0 2px;
              }
              small{
                font-size: 0.8em;
                font-weight: 900;
                text-transform: uppercase;
              }
            }//time
          }//p
        }//meta
        >ul.timer-output{
          display: flex;
          margin-top: 7px;
          >li{
            flex:1;
            padding: 5px;
            figure{
              margin-bottom: 7px;
              position: relative;
              svg{
                transform: rotate(-90deg);
                width: 100%;
                circle{
                  fill: none;
                  &:nth-child(1){
                    stroke-width: 2;
                    stroke:white;
                  }
                  &:nth-child(2){
                    stroke-width: 6;
                    stroke:white;
                  }
                }//circle
              }//svg
              figcaption{
                @include m.posLT(absolute,50%,50%,-50%,-50%);
                font-size: 1.5em;
                font-weight: 900;
              }
            }//fgiure
            p{
              text-align: center;
              font-size: 0.8em;
              em{
                display: block;
                font-weight: 600;
                text-transform: uppercase;
              } 
              small{

              }
            }//p
          }//>li
        }//ul.timer-output
      }//li
    }//ul

    .no-msg{
      @include m.posLT(absolute,50%,50%,-50%,-50%);
      width: 90%;
      text-align: center;
      font-weight: 600;
      font-size: 1.2em;
    }
  }//article.dday-list
  .btn-add{
    width: 100%;
    padding: 10px 0;
    outline: none;
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
    img{
      display: inline-block;
      width: 30px;
      margin-right: 10px;
      vertical-align: middle;
      mix-blend-mode: multiply;
      filter: grayscale(1) brightness(150%);
    }
    &:focus {
      background: rgba(#fff, 0.1);
    }
    &:active {
      box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
    } //button:active
  }//btn-add

  form.dday-add{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    p{
      padding: 15px 0;
      border-top: 2px groove rgba(#FFF,0.5);
      label{
        display: block;
        margin-bottom: 10px;
        i{
          margin-right: 5px;
        }
      }//label
      input{
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        border: none;
        background: rgba(#fff, 0.4);
        outline: none;
        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
        &:focus {
          background: rgba(#fff, 0.8);
        }
        &::placeholder {
          color: inherit;
        }
        &::-webkit-calendar-picker-indicator{
          filter: invert(10%);
          mix-blend-mode: color-burn;
        }
      }//input
    }//p

    button{
      width: 100%;
      margin-top: 15px;
      padding: 10px 0;
      outline: none;
      box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
      &:focus {
        background: rgba(#fff, 0.1);
      }
      &:active {
        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
      } //button:active
    }//button

    .fa-arrow-left{
      font-size: 1.5em;
      display: block;
      margin: 0 auto;
      margin-top: 20px;
      cursor: pointer;
      transition: all 0.2s;
      &:active{
        transform: scale(1.2);
      }
    }
  }//form.dday-add


  div.now{
    text-align: center;
    padding: 10px;
    background: rgba(#000,0.7);
    border-radius: 5px;
    color:#FFF;
    
    time{
      display: block;
      b,small{
        margin:0 3px;
      }
      small{
        font-size: 0.7em;
        font-weight: 600;
        text-transform: uppercase;
        opacity: 0.7;
      }
    }//time
  }//div
}//section