@use "./mixin" as m;

section.bmi {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  form {
    div.radios {
      p {
        position: relative;
        display: flex;
        input {
          /* display: none; //리액트에서 접근을 못해서 경고메세지 발생 */
          border: none;
          background: none;
          width: 1px;
          height: 1px;
          opacity: 0;
          @include m.posLT(absolute, 50%, 50%, -50%, -50%);
          &:checked + label {
            font-weight: 600;
            text-shadow: 1px 1px 1px rgba(255, 255, 255, 1), -1px -1px 1px rgba(0, 0, 0, 0.1);
            box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
          }
        } //input
        label {
          flex: 1;
          padding: 10px 0;
          text-align: center;
          cursor: pointer;
          box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
          &:first-of-type {
            border-radius: 30px 0 0 30px;
          }
          &:last-of-type {
            border-radius: 0 30px 30px 0;
          }
        } //label
      } //p
    } //div.radios

    > div {
      padding: 15px 0;
      border-bottom: 2px groove rgba(#000, 0.1);
      > label {
        display: block;
        margin-bottom: 10px;
      }
      input[type="number"] {
        width: 100%;
        box-sizing: border-box;
        padding: 10px;
        border: none;
        background: rgba(#fff, 0.4);
        outline: none;
        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
        &:focus {
          background: rgba(#fff, 0.8);
        }
        &::placeholder {
          color: inherit;
          opacity: 0.5;
        }
      } //input[type=number]
    } //div
    button {
      width: 100%;
      padding: 10px 0;
      outline: none;
      box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
      &:focus {
        background: rgba(#fff, 0.1);
      }
      &:active {
        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
      } //button:active
    } //button
  } //form

  div.bmi-result {
    position: absolute;
    left: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    padding: 15px;
    width: 100%;
    height: 100%;
    background: rgba(#fff, 0.8);
    transition: all 0.3s;
    &.active {
      left: 0;
    }
    .output {
      position: relative;
      height: 75%;
      margin-bottom: 30px;
      border: 1px solid #eee;
      &.result {
        background: #fff;
        p{
          @include m.posLT(absolute,50%,50%,-50%,-50%);
          font-size: 3em;
          font-weight: 600;
          opacity: 0.5;
        }
      }//result
      &.err {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        p{
          position: relative;
          margin: 20px 0;
          color:#FFF;
          i{
            color:rgba(#FFF,0.5);
            font-size: 8em;
          }//i
          i:last-child{
            font-size: 15em;
            @include m.posLT(absolute,50%,50%,-50%,-50%);
          }
        }//p
        background: #000;
      }//err
      svg.morph {
        @include m.posLT(absolute, 50%, 50%, -50%, -50%);
        height: 100%;
        .org {
          fill: #8facca;
        }
      } //svg
      .board {
        position: absolute;
        width: 100%;
        bottom: 0;
        opacity: 0.8;
        .pin {
          position: absolute;
          bottom: 0;
          right: 50%;
          width: 30%;
          /* transform: translate(7%,50%) rotate(45deg); */
          transform-origin: 93% center;
          transition: transform 1s;
          transition-timing-function: cubic-bezier(0.64, 0.57, 0.67, 1.53);
        } //pin
      } //board
    } //output

    button {
      padding: 10px;
      background: rgba(#fff, 0.5);
      border: 2px solid #fff;
      box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
      &:active {
        box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 1), inset 3px 3px 5px rgba(0, 0, 0, 0.1);
      } //button:active
    } //button
  } //div.bmi-result
} //section
