body {
  color: #666;
  font-family: "Orbitron", "Noto Sans KR", sans-serif;
  font-size: 15px;
  text-decoration: none;
  font-weight: normal;
  font-style: normal;
}

:root {
  --blue: #00aae5;
  --orange: #f68b1e;
  --green: #8cc63f;
}

html {
  margin-top: 0 !important;
}

#wpadminbar {
  display: none;
}

body {
  height: 100vh;
  background-size: 10px, 100%;

  &.gray {
    background-image: url(../assets/img/pattern2.png), linear-gradient(#f6f6f6, #ddd);
    #root {
      background: linear-gradient(#f6f6f6, #ddd);
      color: #999;
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-thumb {
            background-color: #999;
          }
        } //media hover
      } //ul
    } //root
  } //body.gray

  &.beige {
    background-image: url(../assets/img/pattern3.png), linear-gradient(#fff4eb, #ffe4cf);
    #root {
      background: linear-gradient(#fff4eb, #ffe4cf);
      color: #e6ac7c;
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-thumb {
            background-color: #e6ac7c;
          }
        } //media hover
      }
    } //root
  } //body.beige

  &.skyblue {
    background-image: url(../assets/img/pattern4.png), linear-gradient(#edf6ff, #cce3fb);
    #root {
      background: linear-gradient(#edf6ff, #cce3fb);
      color: #488fd7;
      article.dday-list ul {
        @media (hover: hover) {
          &::-webkit-scrollbar-thumb {
            background-color: #488fd7;
          }
        } //media hover
      }
    } //root
  } //body.skyblue

  #root {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: min(95%, 500px);
    height: min(95%, 800px);
    overflow: hidden;
    overflow-y: auto;
    box-shadow: -2px -2px 5px rgba(255, 255, 255, 1), 3px 3px 5px rgba(0, 0, 0, 0.1);
    article.dday-list ul {
      @media (hover: hover) {
        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.1);
          background-color: #fff;
        }
        &::-webkit-scrollbar {
          width: 6px;
          background-color: #f5f5f5;
        }

      } //media hover
    }//article.dday-list ul
    section {
      min-height: 100%;
      box-sizing: border-box;
      padding: 15px;
      h1,
      h2 {
        margin-bottom: 20px;
        text-align: center;
        font-weight: 900;
        font-size: 1.7em;
        text-shadow: -2px -2px 1px rgba(255, 255, 255, 1), 2px 2px 1px rgba(0, 0, 0, 0.1);
      }
    } //section
    .back-btn {
      z-index: 10;
      position: absolute;
      right: 0;
      top: 0;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 1.3em;
      transform: scale(-1);
      &:hover {
        color: rgba(#fff, 0.5);
        text-shadow: -1px -1px 0px rgba(0, 0, 0, 0.5);
      }
    }
  } //root
} //body

@keyframes cursor {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
