@use 'mixin' as m;
figure.coatch-mark{
  position: absolute;
  width: 100%;
  height: 100%;
  top:0;
  background: #5f5f5f;
  transition: opacity 1s;
  &.active{
    opacity: 0
  }
  img{
    @include m.objectFit(contain);
  }
  figcaption{
    @include m.posLB(absolute,0,3%,0,0);
    width: 100%;
    text-align: center;
    color:#FFF;
    text-transform: uppercase;
    button{
      margin-top:15px;
      padding: 10px 40px;
      border:1px solid #FFF;
      background: rgba(#FFF,0.2);
      transition: all 0.3s;
      &:hover,&:active{
        background: #FFF;
        color:#000;
      }
    }
  }
}//coatch-mark